.webide-container {
  height: 100vh;
  overflow: hidden;
}
.webide-container .source-panel .ant-card-head {
  min-height: 24px;
}
.webide-container .source-panel .ant-card-head-title {
  padding: 4px 0;
}
.webide-container .source-panel .ant-card-body {
  padding: 24px 8px;
}
.webide-container .source-panel .source-item {
  width: 25%;
  display: inline-block;
  text-align: center;
  cursor: move;
}
.webide-container .source-panel .source-item img {
  display: block;
  margin: 0 auto;
}
.webide-container .target-panel {
  border-left: 1px solid #ddd;
  border-right: 1px solid #ddd;
}
.webide-container .target-panel .ant-card {
  width: 100%;
  height: 100%;
}
.webide-container .target-panel .ant-card-head {
  min-height: 34px;
  height: 34px;
}
.webide-container .target-panel .ant-card-head-title {
  padding: 4px 0;
}
.webide-container .target-panel .ant-card-body {
  padding: 0;
  width: 100%;
  height: calc(100% - 34px);
}
.webide-container .target-panel .target-box {
  width: 100%;
  height: 100%;
}
.webide-container .props-panel .ant-card-head {
  min-height: 24px;
}
.webide-container .props-panel .ant-card-head-title {
  padding: 4px 0;
}
.webide-container .props-panel .ant-card-body {
  padding: 24px 8px;
}
